@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-size: 14px!important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root { height: 100%; }

.h-100 { height: 100%!important; }

.bg-theme { background-color: #d20023!important; }
.text-theme { color: #d20023!important; }
hr.underline { border-color: #d20023!important; }

html, body, #app, #app>div {
  height: 100%;
}

.scroll-top {
  z-index: 1031;
  position: fixed;
  bottom: 40px;
  right: 40px;
  color: #fff;
  padding: 4px 12px;
  border: 1px solid #fff;
  background-color: #000;
  cursor: pointer;
  opacity: 0.8;
}
.scroll-top:hover {
  transition: 0.2s;
  opacity: 1;
  bottom: 45px
}

/* Animate CSS */
.animate__delay-02s {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.animate__delay-04s {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
/* End of Animate CSS */

@media only screen and (max-width: 600px) {
  h2 { font-size: 1.5rem!important; }
  h3 { font-size: 1.4rem!important; }
  h5 { font-size: 1rem!important; }
}
#loaders{
	position: fixed;
	width: 100%;
	top: 0;
	height: 100%;
	background-color: rgba(0, 0, 0, .7);
	z-index: 10;
}

.loaders{
	position: absolute;
	top: 50%;
	left: 50%;
    transform: translate(-50%, -50%);
}
/* Navigation */
#nav { transition: .2s ease; z-index: 1400; }
#nav.navbar.navbar-expand-lg.navbar-dark { padding-top: 20px; }
#nav.bg-light { box-shadow: 0px 1px 10px rgba(0, 0, 0, .1); }
#nav.bg-light .navbar-brand { color: #d20023; }
#nav.bg-light .navbar-nav a { color: #878b90; transition: .2s ease; }
#nav.bg-light .navbar-nav a:hover, #nav.bg-light .navbar-nav a.active { color: #d20023; }
a.navbar-brand img { height: 30px; line-height: 66px; }
.navbar-toggler { padding: 3px 9px!important; }
.navbar-toggler-icon { width: 16px!important; height: 16px!important; }
.navbar .navbar-nav a { font-weight: 800!important; }
/* End of Navigation */

/* Footer */
footer {
	height: 60px;
	line-height: 60px;
	font-size: small;
}
footer img { 
	line-height: 60px;
	height: 40px;
}

footer .row {
	margin: 0;
}
/* End of Footer */
.content {
    padding: 80px 0;
}

hr.underline { border-top: 2px solid rgba(0, 0, 0, .2); }
hr.underline.b1 { border-top: 1px solid rgba(0, 0, 0, .2); }
hr.underline { width: 100px; }
hr.underline.light { border-color: #f8f9fa!important; }

.bg-section-2 {
    background-color: #f6f6f6;
}
.bg-section {
    background-image: url(data:image/png;base64,);
    background-position: left;
    height: 160px;
    /* width: 100%; */
    margin-left: -10px;
}
.bg-section.flipY {
    transform: scaleY(-1);
}

/* Home */
.background1{
    background-image: url(/semut-merah-informatika/static/media/bg1.b4152264.jpg);
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
}
.intro{
    width: 100%;
    position: absolute;
    left: 0;
    top: 40%;
    /* margin: -44px 0 0 -170px; */
}
.intro h2 { letter-spacing: 1px; word-spacing: 2px; margin: auto; }
.intro h2 span { font-weight: 800; }
/* End of Home */

/* Services */
#services svg {
    border: 2px solid #ddd;
    padding: 12px;
    border-radius: 100px;
    width: 60px;
    height: 60px;
    margin-bottom: 15px;
    transition: .2s ease;
}
#services svg, #services h5 { color: #ddd; transition: .2s ease; }
#services a:hover svg{ border-color: #d20023!important; color: #fff; }
#services a:hover h5{ color: #fff!important; }
#services a:hover h5 span{ color: #d20023!important; }
.background2{
    background-image: url(/semut-merah-informatika/static/media/bg2.514cba5b.jpg);
    width: 100%;
}
/* End of Services */

#clients img {
    width: 80%;
    max-height: 200px;
    line-height: 200px;
}

#clients #images {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

#clients #images [class*="col"] {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
}

.parallax {
    width: 100%;
  
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}


/* Animate CSS */
.tr-1 { transition: opacity 1s ease .1s, transform 1s ease .1s; }
.tr-2 { transition: opacity 1s ease .2s, transform 1s ease .2s; }
.tr-3 { transition: opacity 1s ease .3s, transform 1s ease .3s; }
.tr-4 { transition: opacity 1s ease .4s, transform 1s ease .4s; }
.tr-5 { transition: opacity 1s ease .5s, transform 1s ease .5s; }

.slideLeft {
    opacity: 0;
    transform: translateX(40%);
}
.slideLeft.enter {
    opacity: 1;
    transform: translateX(0);
}

.slideRight {
    opacity: 0;
    transform: translateX(-40%);
}
.slideRight.enter {
    opacity: 1;
    transform: translateX(0);
}
  
.slideUp {
    opacity: 0;
    transform: translateY(40%);
}
.slideUp.enter {
    opacity: 1;
    transform: translateY(0);
}  

.slideDown {
    opacity: 0;
    transform: translateY(4-0%);
}
.slideDown.enter {
    opacity: 1;
    transform: translateY(0);
}
/* End of Animate CSS */
body {
    font-family: 'Montserrat';
    font-size: 16px;
}
